.mainSlide_search_wrap {
    display: inline-block;
    width: 100%;
    float: left;
  }
  
  .mainSlide_search_wrap_1140 {
    display: block;
    max-width: 1140px;
    margin: auto;
    float: none;
  }
  
  .mainSlide_search_big {
    display: inline-block;
    width: 100%;
    position: relative;
    float: left;
  }
  
  .mainSlide_search_box {
    display: inline-block;
    width: 100%;
    float: left;
    position: relative;
  }
  
  .mainSlide_search_box > i {
    width: 40px;
    height: 40px;
    margin: 9px 15px;
    padding-right: 15px;
    border-right: 1px solid #ddd;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    font-size: 20px;
    z-index: 3;
    text-align: center;
    color: #333;
    line-height: 40px;
  }
  
  #form_search.focus .mainSlide_search_box > i {
    border-color: #0074dd;
  }
  
  .mainSlide_search_box > input {
    display: inline-block;
    width: 100%;
    height: 58px;
    padding: 15px;
    padding-top: 14px;
    padding-left: 70px;
    padding-right: 140px;
    border: none;
    border-radius: 0;
    font-size: 20px;
    float: left;
    outline: none;
    box-shadow: none;
  }
  
  .mainSlide_search_big .btn.btn_search {
    display: inline-block;
    width: 120px;
    height: 48px;
    padding: 0;
    background: #0074dd;
    border: 1px solid #0074dd;
    border-radius: 0;
    line-height: 46px;
    position: absolute;
    top: 5px;
    right: 5px;
    font-weight: bold;
    font-size: 18px;
    color: #fff;
  }
  
  .mainSlide_search_big .btn.btn_search:hover {
    background: #fff;
    border-color: #0074dd;
    color: #0074dd;
    transition: all 0.2s;
  }