  .header_wrap {
    display: inline-block;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid #ddd;
    position: relative;
    box-shadow: 0 0 5px rgba(0,0,0,.1);
    float: left;
  }

  .header_category_wrap {
    background: #fff;
    text-align: center;
  }

  .header_category_wrap {
