    width: 100%;
    background: #f6f6f6;
    float: left;
  }

  .header_category_wrap > .header_top_wrap {
    width: 100%;
    padding: 5px 0;
    background: #f6f6f6;
    text-align: center;
    float: left;
  }

  .header_category_wrap > * {
    display: flex;
    width: 1140px;
    margin: 0 auto;
